'use client';

import React, { useState } from 'react';
import useSWR from 'swr';
import {
    Search, Plus, Minus, ShoppingCart, Trash2,
    Utensils, Coffee, Pizza, CreditCard, Banknote, QrCode
} from 'lucide-react';

const fetcher = (url: string) => fetch(url).then(r => r.json());

interface Product {
    id: string;
    name: string;
    category: string;
    price: number;
    stockQty: number;
    imageUrl?: string;
    description?: string;
}

interface CartItem extends Product {
    qty: number;
}

export default function POSPage() {
    const { data: productsData } = useSWR('/api/products', fetcher);
    const products: Product[] = productsData?.data || [];

    const [activeCategory, setActiveCategory] = useState('Semua');
    const [search, setSearch] = useState('');
    const [cart, setCart] = useState<CartItem[]>([]);

    // Category mapping
    const categoryMap: Record<string, string> = {
        'Semua': 'ALL',
        'Makanan': 'FOOD',
        'Minuman': 'DRINK',
        'Snack': 'SNACK'
    };

    const filteredProducts = products.filter(p => {
        const targetCat = categoryMap[activeCategory];
        const matchCat = activeCategory === 'Semua' || p.category === targetCat;
        const matchSearch = p.name.toLowerCase().includes(search.toLowerCase());
        return matchCat && matchSearch;
    });

    const addToCart = (product: Product) => {
        setCart(prev => {
            const existing = prev.find(p => p.id === product.id);
            if (existing) {
                return prev.map(p => p.id === product.id ? { ...p, qty: p.qty + 1 } : p);
            }
            return [...prev, { ...product, qty: 1 }];
        });
    };

    const updateQty = (id: string, delta: number) => {
        setCart(prev => prev.map(p => {
            if (p.id === id) return { ...p, qty: Math.max(0, p.qty + delta) };
            return p;
        }).filter(p => p.qty > 0));
    };

    const subtotal = cart.reduce((sum, item) => sum + (Number(item.price) * item.qty), 0);
    const tax = subtotal * 0.11;
    const total = subtotal + tax;

    const handleProcess = async () => {
        if (cart.length === 0) return;
        try {
            const payload = {
                paymentMethod: 'CASH',
                items: cart.map(item => ({
                    id: item.id,
                    name: item.name,
                    price: Number(item.price),
                    qty: item.qty,
                    type: 'PRODUCT'
                }))
            };
            const res = await fetch('/api/transactions', {
                method: 'POST',
                body: JSON.stringify(payload)
            });
            if (res.ok) {
                alert('Pesanan diproses ke dapur!');
                setCart([]);
            } else {
                alert('Gagal memproses pesanan');
            }
        } catch (e) {
            alert('Error');
        }
    };

    return (
        <div className="flex flex-1 overflow-hidden relative h-full bg-[#050a07]">
            {/* Main Menu Grid */}
            <main className="flex-1 flex flex-col overflow-hidden">
                {/* Top Bar */}
                <div className="px-6 py-4 border-b border-[#1e3328] bg-[#0f1a14] flex justify-between items-center z-20 shadow-md">
                    <div className="flex gap-2 bg-[#050a07] p-1 rounded border border-[#1e3328]">
                        {['Semua', 'Makanan', 'Minuman', 'Snack'].map((cat) => (
                            <button
                                key={cat}
                                onClick={() => setActiveCategory(cat)}
                                className={`px-4 py-1.5 text-sm font-medium rounded transition-all flex items-center gap-2
                      ${activeCategory === cat ? 'bg-[#22c55e] text-black shadow-sm' : 'text-slate-400 hover:text-white hover:bg-[#16261d]'}
                    `}
                            >
                                {cat === 'Makanan' && <Utensils size={14} />}
                                {cat === 'Minuman' && <Coffee size={14} />}
                                {cat === 'Snack' && <Pizza size={14} />}
                                {cat}
                            </button>
                        ))}
                    </div>
                    <div className="relative group min-w-[300px]">
                        <span className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-500 group-focus-within:text-[#22c55e]">
                            <Search size={18} />
                        </span>
                        <input
                            value={search}
                            onChange={(e) => setSearch(e.target.value)}
                            className="w-full bg-[#16261d] border border-[#1e3328] rounded-full py-2 pl-10 pr-4 text-sm text-white focus:ring-1 focus:ring-[#22c55e] focus:border-[#22c55e] placeholder:text-slate-500 outline-none transition-all"
                            placeholder="Cari menu..."
                        />
                    </div>
                </div>

                {/* Menu Grid */}
                <div className="flex-1 overflow-y-auto p-6">
                    <div className="grid grid-cols-2 md:grid-cols-3 xl:grid-cols-4 gap-4">
                        {filteredProducts.map(item => (
                            <div key={item.id} className="bg-[#0f1a14] border border-[#1e3328] rounded-lg overflow-hidden flex flex-col group hover:border-[#22c55e]/50 transition-all">
                                <div className="aspect-square bg-cover bg-center relative" style={{ backgroundImage: `url("${item.imageUrl || '/api/placeholder/400/320'}")` }}>
                                    <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                                        <button
                                            onClick={() => addToCart(item)}
                                            className="bg-[#22c55e] text-black font-bold py-2 px-6 rounded-full transform translate-y-4 group-hover:translate-y-0 transition-all flex items-center gap-2"
                                        >
                                            <Plus size={18} /> Tambah
                                        </button>
                                    </div>
                                </div>
                                <div className="p-4 flex-1 flex flex-col">
                                    <div className="flex justify-between items-start mb-1">
                                        <h4 className="font-bold text-white text-lg line-clamp-1" title={item.name}>{item.name}</h4>
                                    </div>
                                    <p className="text-xs text-slate-400 mb-3 line-clamp-2 min-h-[2.5em]">{item.description || 'Menu favorit pelanggan.'}</p>
                                    <div className="mt-auto flex items-center justify-between">
                                        <span className="text-[#22c55e] font-bold text-lg">Rp {Number(item.price).toLocaleString()}</span>
                                        <button onClick={() => addToCart(item)} className="p-2 bg-[#16261d] rounded-full text-[#22c55e] hover:bg-[#22c55e] hover:text-black transition-colors border border-[#1e3328]">
                                            <Plus size={16} />
                                        </button>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </main>

            {/* Cart Sidebar */}
            <aside className="w-[360px] bg-[#0f1a14] border-l border-[#1e3328] flex flex-col shrink-0 z-30 shadow-xl">
                <div className="p-5 border-b border-[#1e3328] bg-[#16261d] flex justify-between items-center">
                    <h3 className="text-lg font-bold text-white flex items-center gap-2">
                        <ShoppingCart size={20} className="text-[#22c55e]" /> Pesanan Baru
                    </h3>
                    <span className="text-xs bg-[#22c55e]/20 text-[#22c55e] px-2 py-1 rounded font-mono">
                        #{Math.floor(Math.random() * 10000)}
                    </span>
                </div>

                <div className="flex-1 overflow-y-auto p-4 space-y-3">
                    {cart.length === 0 ? (
                        <div className="flex flex-col items-center justify-center h-full text-slate-500 opacity-50">
                            <ShoppingCart size={48} className="mb-2" />
                            <p className="text-sm">Keranjang kosong</p>
                        </div>
                    ) : (
                        cart.map(item => (
                            <div key={item.id} className="flex gap-3 bg-[#050a07] p-3 rounded border border-[#1e3328]">
                                <div className="size-14 bg-cover bg-center rounded border border-[#1e3328]" style={{ backgroundImage: `url("${item.imageUrl}")` }}></div>
                                <div className="flex-1 min-w-0">
                                    <h4 className="font-bold text-white text-sm truncate mb-1">{item.name}</h4>
                                    <div className="flex items-center justify-between">
                                        <span className="text-xs text-[#22c55e]">Rp {(Number(item.price) * item.qty).toLocaleString()}</span>
                                        <div className="flex items-center gap-3 bg-[#16261d] rounded px-1 py-0.5 border border-[#1e3328]">
                                            <button onClick={() => updateQty(item.id, -1)} className="text-slate-400 hover:text-white p-0.5"><Minus size={12} /></button>
                                            <span className="text-xs font-bold w-4 text-center">{item.qty}</span>
                                            <button onClick={() => updateQty(item.id, 1)} className="text-[#22c55e] p-0.5"><Plus size={12} /></button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))
                    )}
                </div>

                <div className="p-5 border-t border-[#1e3328] bg-[#16261d]">
                    <div className="space-y-2 mb-4 text-sm">
                        <div className="flex justify-between text-slate-400">
                            <span>Subtotal</span>
                            <span>Rp {subtotal.toLocaleString()}</span>
                        </div>
                        <div className="flex justify-between text-slate-400">
                            <span>Pajak (11%)</span>
                            <span>Rp {tax.toLocaleString()}</span>
                        </div>
                        <div className="h-px bg-[#1e3328] my-2"></div>
                        <div className="flex justify-between text-lg font-bold text-white">
                            <span>Total</span>
                            <span className="text-[#22c55e]">Rp {total.toLocaleString()}</span>
                        </div>
                    </div>

                    <div className="flex gap-2">
                        <button
                            className="flex-1 py-3 bg-[#1e3328] text-slate-300 hover:text-white border border-[#22c55e]/30 hover:bg-[#22c55e]/20 rounded font-bold transition-all"
                            onClick={() => setCart([])}
                        >
                            Batal
                        </button>
                        <button
                            disabled={cart.length === 0}
                            onClick={handleProcess}
                            className="flex-[2] py-3 bg-[#22c55e] hover:bg-[#16a34a] disabled:bg-slate-700 disabled:text-slate-500 text-black font-bold rounded shadow-lg shadow-[#22c55e]/20 transition-all active:scale-95"
                        >
                            Proses Pesanan
                        </button>
                    </div>
                </div>
            </aside>
        </div>
    );
}
